.fancybox {

  &--custom {

    .fancybox-slide {
      padding: 0;
    }

    .fancybox-content {
      padding: 24px 16px;
      width: 100%;
      background: #171f2e;

      .table__tr:nth-of-type(n + 2) {
        display: block;
      }
    }

    .fancybox-button {

      svg path {
        fill: #fff;
      }
    }
  }
}
