@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --transition-base: .3s cubic-bezier(.25, .1, .25, 1);
  --primary-gradient: linear-gradient(90deg, #1eca59 0, #4cbfb0 60%, #1eca59 100%);
}

@layer components {
  .btn-primary {
    margin: 0;
    padding: 0 16px;
    display: inline-flex;
    min-height: 58px;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    background: var(--primary-gradient);
    border: 0;
    border-radius: 12px;
  }
  .btn-outline-primary {
    margin: 0;
    padding: 0;
    display: inline-flex;
    min-height: 58px;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    background-image: linear-gradient(#171f2e, #171f2e), var(--primary-gradient);
    background-clip: content-box, border-box;
    background-origin: border-box;
    border: double 1px transparent;
    border-radius: 12px;
    @apply font-grotesk;
  }
  .btn-outline-primary span {
    padding: 0 16px;
    background: var(--primary-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
/* Fix for Fancybox */
/* Plugins */
body.compensate-for-scrollbar {
  overflow: hidden;
}

.fancybox-active {
  height: auto;
}

.fancybox-is-hidden {
  left: -9999px;
  margin: 0;
  position: absolute !important;
  top: -9999px;
  visibility: hidden;
}

.fancybox-container {
  -webkit-backface-visibility: hidden;
  height: 100%;
  left: 0;
  outline: none;
  position: fixed;
  -webkit-tap-highlight-color: transparent;
  top: 0;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  width: 100%;
  z-index: 99992;
}

.fancybox-container * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.fancybox-outer,
.fancybox-inner,
.fancybox-bg,
.fancybox-stage {
